.contact-form {
  width: 100%;
  margin-top: 20px;

  ul {
    padding: 0;
    margin: 0;

    li {
      padding: 0;
      margin: 0;
      list-style: none;
      margin-bottom: 10px;
      opacity: 0;
      overflow: hidden;
      display: block;
      clear: both;
      position: relative;
      animation: fadeInUp 2s 2s;
      animation-fill-mode: forwards;
    }

    li.half {
      width: 49%;
      margin-left: 2%;
      float: left;
      clear: none;

      &:first-child {
        margin-left: 0;
      }
    }
  }

  input[type='text'],
  input[type='email'] {
    width: 100%;
    border: 0;
    background: #115173;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 0 20px;
    box-sizing: border-box;
  }

  textarea {
    width: 100%;
    border: 0;
    background: #115173;
    height: 50px;
    font-size: 16px;
    color: #fff;
    padding: 20px;
    box-sizing: border-box;
    min-height: 150px;
  }

  .flat-button {
    color: #ffd700;
    font-size: 11px;
    letter-spacing: 3px;
    text-decoration: none;
    padding: 8px 10px;
    border: 1px solid #ffd700;
    float: left;
    border-radius: 4px;
    background: 0 0;
    text-transform: uppercase;
    float: right;
    text-align: center;
    margin-right: 10px;
    cursor: pointer;

    &:hover{
      background: #ffd700;
      color: #333;
      transition: all .5s ease-in-out;
    }
  }
}

.map-wrap {
  background: rgba(8, 253, 216, 0.1);
  float: right;
  width: 53%;
  height: 115%;
  margin-top: -3%;
}

.leaflet-container {
  position: relative;
  width: 100%;
  height: 100%;
  opacity: 0;
  animation: backInRight 1s 1.2s;
  animation-fill-mode: forwards;
}

.info-map {
  position: absolute;
  background: #000;
  top: 50px;
  right: 30%;
  z-index: 999999;
  width: 267px;
  padding: 20px;
  color: #fff;
  font-family: 'Helvetica';
  font-size: 17px;
  font-weight: 300;
  opacity: 0;
  animation: fadeIn 1s 1.5s;
  animation-fill-mode: forwards;

  span {
    font-size: 16px;

    span {
      color: #ffd700;
    }
  }
}

@media (max-width: 768px){
  .container.contact-page{
    width: 100%;
    height: 100vh;
    margin-bottom: 50px;
    top: 0;

    .text-zone{
      margin-top: 90px;
      width: 90%;
      left: 20px;
    }

    .map-wrap {
      background-color: rgba(8, 253, 216, 0);
      width: 90%;
      position: absolute;
      bottom: -300px;
      margin-left: 20px;
      padding: 10px;
      box-sizing: border-box;
      height: 40%;
    }
    
    .leaflet-container {
      position: relative;
      width: 100%;
      height: 100%;
      opacity: 0;
      animation: backInRight 1s 1.2s;
      animation-fill-mode: forwards;
      bottom: 60px;
    }
    
    .info-map {
      position: absolute;
      background-color: rgba(8, 0, 0, 0.5);
      top: 90vh;
      bottom: 70px;
      left: 10%;
      margin-top: -120px;
      z-index: 999999;
      width: 180px;
      padding: 20px;
      color: #fff;
      font-family: 'Helvetica';
      font-size: 12px;
      font-weight: 300;
      opacity: 0;
      animation: fadeIn 1s 1.5s;
      animation-fill-mode: forwards;
    
      span {
        font-size: 12px;
        text-align: center;
    
        span {
          color: #ffd700;
        }
      }
    }
  }
}
